@use 'sass:meta';
@use 'settings';
@use 'tools';


@layer elements {
	@include meta.load-css('elements');
}

@layer generic {
	@include meta.load-css('generic');
}

@layer objects {
	@include meta.load-css('objects');
}

@layer components {
	@include meta.load-css('components');
}

@layer utils {
	@include meta.load-css('utils');
}

@layer elements, generic, objects, components, utils;						