@use '../tools/' as tools;

/*
*
*   List for eg. contacts or shopping carts
*
*/

.list {

    &__item { 
        padding: 1rem 0; 
        display: flex;
        align-items: center;
        &:not(:last-child) {
            border-bottom: 4px dotted tools.gray(400);
        }

        p {
            margin: 0;
            padding: 0;
        }
    }

    &__actions {
        display: flex;
        flex: 1;
        justify-self: flex-end;
        text-align: right;
        gap: 1rem;
        justify-content: flex-end;
    }

    &__image {
        object-fit: cover;
        height: 4rem;
        width: 4rem;
        border-radius: 100%;
        margin-right: 1rem;
    }

	&__icon {
		margin-right: 1rem;
		width: 4rem;
		height: 4rem;
		display: grid;
		align-content: center;
		justify-content: center;
		background-color: var(--gray-200);
		border-radius: tools.rounded(md);
		color: var(--gray-800);
	}

	&__image--edgy {
		border-radius: tools.rounded(md);
	}

	&__image--small {
		width: 2rem;
		height: 2rem;
	}

    &__content {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
    }

    &__title {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }

    &__subtitle {
        display: flex;
        justify-content: center;
        flex-direction: column;
        font-size: 0.85rem;
        color: tools.gray(900)
    }

	&__description {
        
        font-size: 0.85rem;
        color: tools.gray(900)
    }

    &__controls {
        margin-left: auto;
        display: flex;
		gap: 1rem;
    }

	&--padded {
		.list__item {
			padding-left: 2rem;
			padding-right: 2rem
		}
	}

}